<template>
    <h1> Interpolation Demo</h1>
    2+2= {{ 2+2 }}<br>
    Hello Length {{ "Hello".length }}
    <input type=button value="+" v-on:click="getData()">
    <input type=button value="-" v-on:dblclick="jfun1()">
    <h1 v-on:mouseover="jfun2()">OnMouseOver Here</h1>
    {{ count }}

</template>
<script>
export default {
    name: 'InterpolationDemo',
    data(){
        return {
            count:0
        }
    },
    methods:{
        getData()
        {
            //alert("Welcome to Vue.Js")
            this.count=this.count+1
        },
        jfun1()
        {
            //alert("Welcome to Vue.Js")
            this.count=this.count-1
        }
}
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
    background-color: rgb(93, 42, 165);
}
</style>
